html {
  --blue-link: #00acdc;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

nav {
  display: block;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__link {
  box-sizing: content-box;
  text-transform: uppercase;
  color: #000;
  white-space: nowrap;
  text-align: left;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__item .nav__link:hover, .nav__link.is-active {
  color: var(--blue-link);
}

.is-active:after {
  content: "";
  background-color: var(--blue-link);
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__item:first-child {
  margin-left: 0;
}

